___CSS_LOADER_EXPORT___.push([module.id, ".form{padding:8px}.form-input{border:1px solid #d3e0e9;outline:none;padding:5px 10px;width:100%}.notice{color:#aaa}", "",{"version":3,"sources":["webpack://./resources/js/components/ChatFormComponent.vue"],"names":[],"mappings":"AA+FA,MACA,WACA,CACA,YAEA,wBAAA,CAEA,YAAA,CADA,gBAAA,CAFA,UAIA,CACA,QACA,UACA","sourcesContent":["<template>\r\n  <form class=\"form\">\r\n    <textarea\r\n      id=\"body\"\r\n      cols=\"28\"\r\n      rows=\"5\"\r\n      class=\"form-input\"\r\n      @keydown=\"typing\"\r\n      v-model=\"body\"\r\n    >\r\n    </textarea>\r\n    <label\r\n      >Attach a File:\r\n      <input type=\"file\" ref=\"uploadInput\" multiple accept=\"image/*\" />\r\n    </label>\r\n    <span class=\"notice\"> Hit Return to send a message </span>\r\n    <span> </span>\r\n  </form>\r\n</template>\r\n\r\n<script src=\"/xampp/htdocs/ChatApp/node_modules/socketio-file-upload/client.js\"></script>\r\n<script>\r\nimport Event from \"../events.js\";\r\n\r\nconst { io } = require(\"socket.io-client\");\r\nconst SocketIOFileUpload = require(\"socketio-file-upload\");\r\nexport default {\r\n  data() {\r\n    return {\r\n      body: null,\r\n      attachment: null,\r\n      with_attach: null,\r\n    };\r\n  },\r\n  mounted() {\r\n    this.socketInstance = io(\"http://localhost:3000\");\r\n    var siofu = new SocketIOFileUpload(this.socketInstance);\r\n    siofu.listenOnInput(this.$refs.uploadInput);\r\n\r\n    siofu.addEventListener(\"progress\", (event) => {\r\n      var percent = (event.bytesLoaded / event.file.size) * 100;\r\n      console.log(\"File is\", percent.toFixed(2), \"percent loaded\");\r\n    });\r\n    siofu.addEventListener(\"complete\", (event) => {\r\n      console.log(event.detail.nameOfImage);\r\n      // var img = document.createElement(\"img\");\r\n      // img.src = event.detail.pathName;\r\n      this.attachment = event.detail.pathName;\r\n      this.with_attach = true;\r\n    });\r\n  },\r\n  methods: {\r\n    typing(e) {\r\n      if (e.keyCode === 13 && !e.shiftKey) {\r\n        e.preventDefault();\r\n        this.sendMessage();\r\n      }\r\n    },\r\n    sendMessage() {\r\n      if (!this.body || this.body.trim() === \"\") {\r\n        return;\r\n      }\r\n      let messageObj = this.buildMessage();\r\n      axios\r\n        .post(\"/message\", {\r\n          id: Date.now(),\r\n          body: this.body,\r\n          attachment: this.attachment,\r\n          with_attach: this.with_attach,\r\n          selfMessage: true,\r\n          name: Laravel.user.name,\r\n        })\r\n        .catch(() => {\r\n          console.log(\"failed\");\r\n        });\r\n      this.socketInstance = io(\"http://localhost:3000\");\r\n      this.socketInstance.emit(\"message\", messageObj);\r\n      this.body = null;\r\n      this.attachment = null;\r\n    },\r\n    buildMessage() {\r\n      return {\r\n        id: Date.now(),\r\n        body: this.body,\r\n        attachment: this.attachment,\r\n        with_attach: this.with_attach,\r\n        selfMessage: true,\r\n        name: Laravel.user.name,\r\n      };\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style>\r\n.form {\r\n  padding: 8px;\r\n}\r\n.form-input {\r\n  width: 100%;\r\n  border: 1px solid #d3e0e9;\r\n  padding: 5px 10px;\r\n  outline: none;\r\n}\r\n.notice {\r\n  color: #aaa;\r\n}\r\n</style>"],"sourceRoot":""}]);
